import React, { useState, useEffect } from "react";
import Scrollbars from "react-custom-scrollbars";
import { Textarea, Select, Input } from "@windmill/react-ui";
import ReactTagInput from "@pathofdev/react-tag-input";

import Title from "../form/Title";
import Error from "../form/Error";
import LabelArea from "../form/LabelArea";
import InputArea from "../form/InputArea";
import InputValue from "../form/InputValue";
import SelectOption from "../form/SelectOption";
import DrawerButton from "../form/DrawerButton";
import Uploader from "../image-uploader/Uploader";
import ChildrenCategory from "../category/ChildrenCategory";
import ParentCategory from "../category/ParentCategory";
import useProductSubmit from "../../hooks/useProductSubmit";
import CategoryServices from "../../services/CategoryServices";

const quantityData = {
  unit: "",
  quantity: "",
};

const ProductDrawer = ({ id }) => {
  const [formInputs, setFormInputs] = useState([{}]);
  const [category, setcategoryList] = useState([]);
  const [selectCategory, setselectCategory] = useState();

  const onChange = (e, index) => {
    const { name, value } = e.target;
    console.log(name, value);
    let data = [...formInputs];
    if (typeof data[index] === "undefined") {
      data.push({});
    }
    data[index][name] = value;
    setFormInputs(data);
    console.log("onchange wrks prdt...");
  };

  const onAddClick = () => {
    setFormInputs([...formInputs, { ...quantityData }]);
  };

  const onRemoveClick = (index) => {
    const data = [...formInputs];
    data.splice(index, 1);
    setFormInputs(data);
  };

  const handleSelect = async (e) => {
    setselectCategory(e.target.value);
  };
  const {
    register,
    watch,
    handleSubmit,
    onSubmit,
    errors,
    imageUrl,
    setImageUrl,
    tag,
    metatag,
    setTag,
    setMetaTag,
    showRental,
    setShowRental,
  } = useProductSubmit(id, formInputs, selectCategory);

  useEffect(async () => {
    let categoryList = await CategoryServices.getAllCategory();
    setcategoryList(categoryList);
    console.log(categoryList, "===ctglist");
  }, []);
  return (
    <>
      <div className="w-full relative p-6 border-b border-gray-100 bg-gray-50 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-300">
        {id ? (
          <Title
            title="Update Product"
            description="Updated your product and necessary information from here"
          />
        ) : (
          <Title
            title="Add Product"
            description="Add your product and necessary information from here"
          />
        )}
      </div>
      <Scrollbars className="w-full md:w-7/12 lg:w-8/12 xl:w-8/12 relative dark:bg-gray-700 dark:text-gray-200">
        <form onSubmit={handleSubmit(onSubmit)} className="block">
          <div className="px-6 pt-8 flex-grow w-full h-full max-h-full pb-40 md:pb-32 lg:pb-32 xl:pb-32">
            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Product Image" />
              <div className="col-span-8 sm:col-span-4">
                <Uploader imageUrl={[]} setImageUrl={setImageUrl} />
              </div>
            </div>

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Product Title/Name" />
              <div className="col-span-8 sm:col-span-4">
                <InputArea
                  register={register}
                  label="Product Title/Name"
                  name="title"
                  type="text"
                  placeholder="Product title"
                />
                <Error errorName={errors.title} />
              </div>
            </div>

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Product Slug" />
              <div className="col-span-8 sm:col-span-4">
                <InputArea
                  register={register}
                  required="false"
                  label="Product Slug"
                  name="slug"
                  type="text"
                  placeholder="Product slug"
                />
                <Error errorName={errors.slug} />
              </div>
            </div>

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Product Description" />
              <div className="col-span-8 sm:col-span-4">
                <Textarea
                  className="border text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                  {...register("description", {
                    required: "Description is required!",
                    minLength: {
                      value: 20,
                      message: "Minimum 20 character!",
                    },
                  })}
                  name="description"
                  placeholder="Product description"
                  rows="4"
                  spellCheck="false"
                />
                <Error errorName={errors.description} />
              </div>
            </div>
            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Color" />
              <div className="col-span-8 sm:col-span-4">
                <InputArea
                  register={register}
                  defaultValue=""
                  label="Color"
                  name="color"
                  type="text"
                  placeholder="Color"
                />
                <Error errorName={errors.color} />
              </div>
            </div>
            {/* sku */}
            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Sku" />
              <div className="col-span-8 sm:col-span-4">
                <InputArea
                  register={register}
                  defaultValue=""
                  label="Sku"
                  name="sku"
                  type="text"
                  placeholder="Sku"
                />
                <Error errorName={errors.color} />
              </div>
            </div>
            {/* sku */}
            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Material" />
              <div className="col-span-8 sm:col-span-4">
                <InputArea
                  register={register}
                  defaultValue=""
                  label="Material"
                  name="material"
                  type="text"
                  placeholder="Material"
                />
                <Error errorName={errors.color} />
              </div>
            </div>
            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Category" />
              <div className="col-span-8 sm:col-span-4">
                <select onChange={handleSelect} name="category" className="w-full py-3 border rounded">
                  <option selected hidden>
                    select category
                  </option>
                  {category.map((val) => (
                    
                    <option value={val.parent}>{val.parent}</option>
                  ))}
                </select>
                <Error errorName={errors.category} />
              </div>
            </div>

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Type" />
              <div className="col-span-8 sm:col-span-4">
                <InputArea
                  register={register}
                  defaultValue=""
                  label="Type"
                  name="type"
                  type="text"
                  placeholder="Type"
                />
                <Error errorName={errors.brand} />
              </div>
            </div>

            {formInputs.map((element, index) => {
              return (
                <div>
                  <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                    <LabelArea label="Unit (inch/cm/mm/feet...etc)" />
                    <div className="col-span-8 sm:col-span-4">
                      <input
                        // register={register}
                        value={element.unit}
                        onChange={(e) => onChange(e, index)}
                        label="Unit"
                        name="unit"
                        type="text"
                        placeholder="Unit"
                        className="w-full p-2 border rounded "
                      />
                      <Error errorName={errors.unit} />
                    </div>
                  </div>

                  <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
                    <LabelArea label="Product Quantity" />
                    <div className="col-span-8 sm:col-span-4">
                      <input
                        // register={register}
                        value={element.quantity}
                        onChange={(e) => onChange(e, index)}
                        maxValue={1000}
                        minValue={0}
                        label="Quantity"
                        name="quantity"
                        type="number"
                        placeholder="Quantity"
                        className="w-full p-2 border rounded "
                      />
                      <Error errorName={errors.quantity} />
                    </div>
                  </div>
                </div>
              );
            })}
            <div className="my-4 text-right">
            {
              <button
                style={{
                  width: "8rem",
                  backgroundColor: "#0E9F6E",
                  outlineColor: "#0E9F6E",
                  color: "white",
                  borderRadius: "0.5rem",
                  marginRight: "1rem",
                }}
                type="button"
                onClick={onAddClick}
                className="py-1"
              >
                Add
              </button>
            }
            {
              <button
                style={{
                  width: "8rem",
                  backgroundColor: "#E5E7EB",
                  outlineColor: "#E5E7EB",
                  color: "red",
                  borderRadius: "0.5rem",
                }}
                type="button"
                onClick={(index) => onRemoveClick(index)}
                className="py-1"
              >
                Remove
              </button>
            }
            </div>

            {/* <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Unit (inch/cm/mm/feet...etc)" />
              <div className="col-span-8 sm:col-span-4">
                <InputArea
                  register={register}
                  label="Unit"
                  name="unit"
                  type="text"
                  placeholder="Unit"
                />
                <Error errorName={errors.unit} />
              </div>
            </div> */}

            {/* <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Product Quantity" />
              <div className="col-span-8 sm:col-span-4">
                <InputValue
                  register={register}
                  maxValue={1000}
                  minValue={0}
                  label="Quantity"
                  name="quantity"
                  type="number"
                  placeholder="Quantity"
                />
                <Error errorName={errors.quantity} />
              </div>
            </div> */}

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Product Price" />
              <div className="col-span-8 sm:col-span-4">
                <InputValue
                  register={register}
                  minValue={1}
                  label="Price"
                  name="originalPrice"
                  type="number"
                  placeholder="Price"
                />
                <Error errorName={errors.originalPrice} />
              </div>
            </div>

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Sale Price" />
              <div className="col-span-8 sm:col-span-4">
                <InputValue
                  register={register}
                  minValue={1}
                  defaultValue="0"
                  required="false"
                  label="Sale price"
                  name="price"
                  type="number"
                  placeholder="Sale price"
                />
                <Error errorName={errors.price} />
              </div>
            </div>

            {/* <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Stock Status" />
              <div className="col-span-8 sm:col-span-4">
                <InputValue
                  register={register}
                  defaultValue=""
                  required="false"
                  label="Stock Status"
                  name="status"
                  type="text"
                  placeholder="Stock Status"
                />
                <Error errorName={errors.stock_status} />
              </div>
            </div> */}

            <div className="grid grid-cols-6 gap-3 md:gap-5 xl:gap-6 lg:gap-6 mb-6">
              <LabelArea label="Product Tag" />
              <div className="col-span-8 sm:col-span-4">
                <ReactTagInput
                  placeholder="Product Tag (Write then press enter to add new tag )"
                  tags={tag ? tag : []}
                  onChange={(newTags) => setTag(newTags)}
                />
              </div>
            </div>
          </div>

          <DrawerButton id={id} title="Product" />
        </form>
      </Scrollbars>
    </>
  );
};

export default React.memo(ProductDrawer);
